import template from '../template.js';

/**
 *  jsmod ui 
 * MIT Licensed
 */
let _option = {
  currentPage: 0,
  maxShowPage: 10,
  textLabel: ['', '', '', ''],
  pageLabel: '{#0}',
  preventInitEvent: false
};

const PAGE_TPL = '' +
 '<div class="mod-page">' +
   '<% for (var i = 0; i < renderDatas.length; i++) { %>' +
     '<a href="javascript:void(0);" <% if (renderDatas[i].page !== undefined) { %> data-page="<%= renderDatas[i].page %>" <% } %> class="mod-page-item <%= renderDatas[i].className %>"><%= renderDatas[i].label %></a>' +
   '<% } %>' +
 '</div>';

/**
 *  html  div 
 * @alias module:jsmod/ui/pagination
 * @constructor
 * @param {(dom|string)}      element                                                          
 * @param {object}            option                                                           
 * @param {int}               option.pageCount                                                 
 * @param {int}               [option.currentPage=0]                                           
 * @param {int}               [option.maxShowPage=10]                                          
 * @param {array}             [option.textLabel=new Array('', '', '', '')] 
 * @param {(string|function)} [option.pageLabel={#0}]                                           {#0} ,  page 0 page + 1
 * @param {bool}              [option.preventInitEvent=false]                                  
 * @param {bool}              [option.allwaysShow=false]                                       
 * @example
 * var Pagination = require("jsmod/ui/pagination");
 *
 * // 
 * new Pagination("#page-container", {pageCount: 20});
 */
export default class Pagination {
  constructor (element, option) {
    var self = this;

    self.element = $(element);
    self.option = $.extend({}, _option, option);

    self.generatePage();
  }
  /**
   * @private
   * @description htmlevent
   */
  generatePage () {
    var self = this,
      option = self.option,
      renderDatas, html;

    self.generateEvents();

    if (option.pageCount < option.maxShowPage) {
      option.maxShowPage = option.pageCount;
    }

    if (option.preventInitEvent) {
      self.setPage(option.currentPage);
    } else {
      // page
      setTimeout(function() {
        self.setPage(option.currentPage);
      }, 0);
    }
  }

  /**
   * 
   * @public
   * @param {int} page 
   * @fires module:jsmod/ui/pagination#page
   */
  setPage (page, preventDisptach) {
    var self = this,
      html, e;

    html = self.getHTML(self.getRenderDatas(page));
    self.element.html(html);
    e = $.Event("page", {page: self.currentPage});

    /**
     * pagepage
     * @event module:jsmod/ui/pagination#page
     * @type {object}
     * @property {int} page page
     */
    if (!preventDisptach) {
      $(self).trigger(e, [{page: self.currentPage}]);
    }
  }

  /**
   *  page
   * @public
   */
  getPage () {
    return this.currentPage;
  }

  /**
   * @private
   * @description 
   */
  generateEvents () {
    var self = this,
      element = self.element,
      option = self.option;

    element.undelegate("click.page");
    element.delegate("[data-page]:not(.mod-page-item-disabled)", "click.page", function(e) {
      var page = $(this).data("page");

      if ($.isNumeric(page)) {
        self.setPage(page);
      } else if (page == "+") {
        self.setPage(self.currentPage + 1);
      } else if (page == "-") {
        self.setPage(self.currentPage - 1);
      }

      return false;
    });
  }

  /**
   * 
   * @public
   */
  destroy () {
    this.element.undelegate("click.page");
    this.element.html("");
  }

  /**
   * @private
   * @description HTML
   * @param {array} renderDatas 
   */
  getHTML (renderDatas) {
    var html;

    html = template(PAGE_TPL, {renderDatas: renderDatas});
    return html;
  }

  /**
   * @private
   * @description 
   * @param {int} page 
   * @return {array} renderDatas 
   */
  getRenderDatas (page) {
    var self = this,
      option = self.option,
      renderDatas = [],
      start, end, offsetEnd, offsetStart;

    page = parseInt(page);
    page = page < 0 ? 0 : page;
    page = page > option.pageCount - 1 ? option.pageCount - 1 : page;

    var flag = parseInt(option.maxShowPage / 3); // 

    start = page - flag < 0 ? 0 : page - flag; // start 
    offsetEnd = page - flag < 0 ? Math.abs(page - flag) : 0; // end 

    end = page + (option.maxShowPage - flag) - 1 > option.pageCount - 1 ? option.pageCount - 1 : page + (option.maxShowPage - flag) -1; // end 
    offsetStart = page + (option.maxShowPage - flag) - 1 > option.pageCount - 1 ? Math.abs(page + (option.maxShowPage - flag) - 1 - (option.pageCount - 1)) : 0 // start 

    start -= offsetStart;
    end += offsetEnd;

    if (page != 0 || option.allwaysShow) {
      // 
      $.each(option.textLabel.slice(0, 2), function(i, label) {
        if (i == 0 && label) {
          renderDatas.push({
            className: (page == 0) ? 'mod-page-item-first mod-page-item-disabled' : "mod-page-item-first",
            label: label,
            page: 0
          });
        }
        if (i == 1 && label) {
          renderDatas.push({
            className: (page == 0) ? "mod-page-item-prev mod-page-item-disabled" : "mod-page-item-prev",
            label: label,
            page: "-"
          });
        }
      });
    }

    // 
    for (start; start <= end; start++) {
      renderDatas.push({
        className: start == page ? "mod-page-item-active" : "",
        label: $.isFunction(option.pageLabel) ? option.pageLabel(start) : option.pageLabel.replace(/{#0}/g, start + 1),
        page: start
      });
    }

    if (page != option.pageCount - 1 || option.allwaysShow) {
      // 
      $.each(option.textLabel.slice(2, 4), function(i, label) {
        if (i == 0 && label) {
          renderDatas.push({
            className: (page == option.pageCount - 1) ? 'mod-page-item-next mod-page-item-disabled' : "mod-page-item-next",
            label: label,
            page: "+"
          });
        }
        if (i == 1 && label) {
          renderDatas.push({
            className: (page == option.pageCount - 1) ? 'mod-page-item-last mod-page-item-disabled' : "mod-page-item-last",
            label: label,
            page: option.pageCount - 1
          });
        }
      });
    }

    // 
    self.currentPage = page;

    return renderDatas;
  }
}



